.about-us {
  display: flex;

  @media (min-width: 1024px) {
    margin-bottom: 99px;
  }

  @media (min-width: 768px) {
    margin-bottom: 80px;
  }
}

.about-us img {
  @media (min-width: 1024px) {
    max-height: 320px;
    margin-right: 251px;
    margin-left: 0;
    margin-top: 0;
    padding-bottom: 0;
  }

  @media (min-width: 768px) {
    display: block;
    max-height: 382px;
    margin-right: 29px;
    margin-left: -4px;
    margin-top: 5px;
    padding-bottom: 55px;
  }

  @media (min-width: 320px) {
    display: none;
  }
}

.about-us__info h2 {
  padding: 0;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 51px;
  color: #041827;

  @media (min-width: 768px) {
    font-size: 36px;
    margin-bottom: 12px;
  }

  @media (min-width: 320px) {
    font-size: 28px;
    margin-bottom: 7px;
  }
}

.about-us__info p {
  padding: 0;
  margin: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  color: #041827;

  @media (min-width: 1024px) {
    margin-bottom: 21px;
  }

  @media (min-width: 768px) {
    line-height: 22px;
    font-size: 16px;
    margin-bottom: 16px;
  }

  @media (min-width: 320px) {
    font-size: 14px;
    line-height: 134.4%;

    &:not(:last-of-type) {
      margin-bottom: 19px;
    }
  }
}

.about-us__info button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #00b2ff;
  background: inherit;
  border: 1px solid #00b2ff;
  border-radius: 3px;

  @media (min-width: 1024px) {
    margin-top: 24px;
  }

  @media (min-width: 768px) {
    padding: 13px 49px;
    margin-top: 16px;
    margin-bottom: 0;
  }

  @media (min-width: 320px) {
    padding: 9px 40px;
    font-size: 14px;
    margin-top: 13px;
    margin-bottom: 80px;
  }
}

.about-us__info-description span {
  @media (min-width: 768px) {
    display: inline;
  }

  @media (min-width: 320px) {
    display: none;
  }
}

.about-us__info .about-us__info-description-drop-info {
  margin-top: 20px;
}
